import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { FC, useState } from 'react';
import BootstrapTable from 'react-bootstrap-table-next';
import ToolkitProvider from 'react-bootstrap-table2-toolkit';
import { Button, Card, CardBody, CardHeader, UncontrolledTooltip } from 'reactstrap';
import { ACTION_EVENTS, alertManager, AlertType, UseFormProps } from 'summer';
import { LancamentoHistorico } from '../../../../models/dtos/mcg/lancamentoHistorico';

import 'react-bootstrap-table-next/dist/react-bootstrap-table2.min.css';
import 'react-bootstrap-table2-toolkit/dist/react-bootstrap-table2-toolkit.min.css';
import './HistoricoVariavelDataGrid.css';

interface HistoricoVariavelDataGridProps {
  formProps: UseFormProps;
  onEdit: (historio: LancamentoHistorico & { rowIndex: number }) => void;
  onRemove: (historio: LancamentoHistorico & { rowIndex: number }) => void;
  disabled: boolean;
}

const HistoricoVariavelDataGrid: FC<HistoricoVariavelDataGridProps> = props => {
  const [selecionado, setSelecionado] = useState(null);

  const columns = [
    {
      dataField: 'historico.codigo',
      text: 'Cdigo',
    },
    {
      dataField: 'historico.descricao',
      text: 'Descrio',
    },
    {
      dataField: 'historicoVariavel',
      text: 'Histrico Varivel',
    },
  ];

  const historicos = props.formProps.watch('historicos').map((it, rowIndex) => ({ ...it, rowIndex }));

  const onSelect = (row, _, index) => setSelecionado({ ...row, rowIndex: index + 1 });

  const checkSelect = () => {
    const result = selecionado !== null;
    if (result === false) {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message: 'Selecione um registro',
        type: AlertType.INFORMATION,
      });
    }
    return result;
  };

  const onEdit = () => {
    if (checkSelect()) {
      props.onEdit(selecionado);
    }
  };

  const onRemove = () => {
    if (checkSelect()) {
      props.onRemove(selecionado);
    }
  };

  const onSort = direction => () => {
    if (checkSelect()) {
      const currIndex = selecionado.rowIndex - 1;
      const nextIndex = currIndex + direction;

      historicos.splice(currIndex, 1);
      historicos.splice(nextIndex, 0, selecionado);

      setSelecionado({ ...selecionado, rowIndex: nextIndex + 1 });

      props.formProps.setValue('historicos', [...historicos]);
    }
  };

  return (
    <Card className="card-box grid-historicos">
      <CardHeader className="card-header pr-2">
        <div className="card-header--title">Histricos</div>
        <div className="card-header--actions">
          <Button id="moveUpButton" size="sm" color="link" className="text-success" onClick={onSort(-1)}>
            <FontAwesomeIcon icon="long-arrow-alt-up" />
          </Button>
          <UncontrolledTooltip target="moveUpButton">Mover - Acima</UncontrolledTooltip>
          <Button id="moveDownButton" size="sm" color="link" className="text-success" onClick={onSort(1)}>
            <FontAwesomeIcon icon="long-arrow-alt-down" />
          </Button>
          <UncontrolledTooltip target="moveDownButton">Mover - Abaixo</UncontrolledTooltip>
          <Button id="editHistButton" size="sm" color="link" className="text-warning" onClick={onEdit} disabled={props.disabled}>
            <FontAwesomeIcon icon="pen" />
          </Button>
          <UncontrolledTooltip target="editHistButton">Editar Histrico Selecionado</UncontrolledTooltip>
          <Button id="removeHistButton" size="sm" color="link" className="text-danger" onClick={onRemove} disabled={props.disabled}>
            <FontAwesomeIcon icon="trash" />
          </Button>
          <UncontrolledTooltip target="removeHistButton">Excluir Histrico Selecionado</UncontrolledTooltip>
        </div>
      </CardHeader>
      <CardBody>
        <ToolkitProvider keyField="rowIndex" columns={columns} data={historicos} bootstrap4={true}>
          {toolkitprops => [
            <BootstrapTable
              {...toolkitprops.baseProps}
              striped={true}
              bordered={true}
              hover={true}
              responsive={true}
              condensed={true}
              key="histVariavelDados"
              selectRow={{
                clickToSelect: true,
                mode: 'radio',
                onSelect,
                selected: selecionado ? [selecionado.rowIndex - 1] : [],
              }}
              rowEvents={{
                onDoubleClick: () => onEdit(),
              }}
            />,
            <div key="divFotterKey" className="text-left">
              Registros: {historicos.length}
            </div>,
          ]}
        </ToolkitProvider>
      </CardBody>
    </Card>
  );
};

export default HistoricoVariavelDataGrid;
