import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import cx from 'clsx';
import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { useSelector } from 'react-redux';
import { RouteComponentProps } from 'react-router';
import { Button, Card, CardBody, CardHeader, Col, Input, Row, UncontrolledTooltip } from 'reactstrap';
import {
  ACTION_EVENTS,
  alertManager,
  AlertType,
  Box,
  CALIMA_DANGER,
  Checkbox,
  compare,
  EditForm,
  EditViewProps,
  ErroList,
  findById,
  formatDate,
  formatRef,
  formatSaldo,
  getSelectedItem,
  InputGroupInline,
  InputType,
  isEmpty,
  parseRefToDate,
  refToFormat,
  toDate,
  WindowComponent,
} from 'summer';
import { CentroCusto } from '../../../../models/dtos/ger/centroCusto';
import { PlanoConta } from '../../../../models/dtos/ger/planoConta';
import { Lancamento } from '../../../../models/dtos/mcg/lancamento';
import { LancamentoHistorico } from '../../../../models/dtos/mcg/lancamentoHistorico';
import { CLASS_NAME, LancamentoLote } from '../../../../models/dtos/mcg/lancamentoLote';
import { LancamentoMCGConfig } from '../../../../models/dtos/mcg/lancamentoMCGConfig';
import { LotePadrao } from '../../../../models/dtos/mcg/lotePadrao';
import { TRIBUTACAO_LUCRO_REAL_ENUM_VALUES } from '../../../../models/enumerated/ger/tipoTributacaoEnum';
import { PERIODO_ANTERIOR } from '../../../../models/enumerated/mcg/bloqueioLancamentoValidateStringEnum';
import { EFEITO_PATRIMONIO_LIQUIDO_ENUM_VALUES, NAO_INTERFERE } from '../../../../models/enumerated/mcg/efeitoPatrimonioLiquidoEnum';
import { LANCAMENTO_UNICO, VARIOS_LANCAMENTOS } from '../../../../models/enumerated/mcg/lancamentoConfigHistoricoTypeEnum';
import {
  LANCAMENTO_MCG_FORMA_ENUM_VALUES,
  UM_CREDITO_VARIOS_DEBITOS,
  UM_DEBITO_UM_CREDITO,
  UM_DEBITO_VARIOS_CREDITOS,
  VARIOS_DEBITOS_VARIOS_CREDITOS,
} from '../../../../models/enumerated/mcg/lancamentoMCGFormaEnum';
import { NAO, SIM } from '../../../../models/enumerated/mcg/lancamentoMCGTipoValidLancPartidaSimplesEnum';
import { CONTABILIDADE } from '../../../../models/enumerated/mcg/origemLancamentoMCGEnum';
import { CONTABIL } from '../../../../models/enumerated/mlf/tipoApuracaoEnum';
import { atualizaCalculoIrpjCsll, getSaldoCredito, getSaldoDebito, isAlteraCalculoIRPJorCSLL } from '../../../../services/mcg.service';
import { Reducers } from '../../../../store/ducks';
import { GlobalState } from '../../../../store/ducks/global.duck';
import EnumUtil from '../../../../utilities/enum.util';
import { concatenarHistorico, validatePeriodoConfig } from '../../../../utilities/mcg.util';
import CentroCustoIS from '../../../components/mcg/centroCustoIS/CentroCustoIS';
import HistoricoIS from '../../../components/mcg/historicoIS/HistoricoIS';
import LancamentoMCGConfigIS from '../../../components/mcg/lancamentoMCGConfigIS/LancamentoMCGConfigIS';
import LancamentoPadraoIS from '../../../components/mcg/lancamentoPadraoIS/LancamentoPadraoIS';
import PlanoContaIS from '../../../components/mcg/planoContaIS/PlanoContaIS';
import HistoricoVariavelDataGrid from './HistoricoVariavelDataGrid';
import LancamentoItensDataGrid from './LancamentoItensDataGrid';
import LancamentoPadraoModalView from './LancamentoPadraoModalView';

import './LancamentoSaldoEditView.css';

interface LancamentoSaldoEditViewProps extends EditViewProps<LancamentoLote> {}

interface ScreenFields {
  centroCustoCredito?: CentroCusto;
  centroCustoDebito?: CentroCusto;
  configuracao?: LancamentoMCGConfig & { label?: string };
  data?: string;
  efeitoPatrimonioLiquido?: string;
  encerrado?: boolean;
  formaLancamento?: number;
  historicoAtual?: LancamentoHistorico;
  historicos?: LancamentoHistorico[];
  historicoVariavel?: string;
  lancamentoPadrao?: LotePadrao;
  numeroLancamento?: string;
  planoContaCredito?: PlanoConta;
  planoContaDebito?: PlanoConta;
  referencia?: string;
  somaCredito?: string;
  somaDebito?: string;
  idLancamentoAtual?: number;
  indexLancamentoAtual?: number;
  idHistoricoAtual?: number;
  indexHistoricoAtual?: number;
  valor?: number;
}

interface CamposFixos {
  fixarCentroCustoCredito?: boolean;
  fixarCentroCustoDebito?: boolean;
  fixarContaCredito?: boolean;
  fixarContaDebito?: boolean;
  fixarData?: boolean;
  fixarHistoricoPadrao?: boolean;
  fixarHistoricoVariavel?: boolean;
}

interface OpenFromVerifyError {
  idMcgLancamentoLote: number;
  isFromVerifyError: true;
}

const LancamentoSaldoEditView: FC<LancamentoSaldoEditViewProps & RouteComponentProps & OpenFromVerifyError> = props => {
  const globalReducer = useSelector<Reducers, GlobalState>(state => state.globalReducer);

  const selectedItem = props.selectedItem || getSelectedItem(props);

  const formProps = useForm<LancamentoLote & ScreenFields & CamposFixos>({
    defaultValues: {
      historicos: [],
      lancamentos: [],
      somaCredito: '0,00',
      somaDebito: '0,00',
    },
    mode: 'onBlur',
  });

  const [lancamentoConfig, setLancamentoConfig] = useState<LancamentoMCGConfig>(globalReducer.globalParameter.lancamentoMCGConfig);
  const [bloquearReferencia, setBloquearReferencia] = useState(null);
  const [patrimonioLiquidoVisible, setPatrimonioLiquidoVisible] = useState(false);
  const [modalContent, setModalContent] = useState(null);
  const [labelDataInclusao, setLabelDataInclusao] = useState(null);
  const [isEdit, setIsEdit] = useState(false);
  const [isEditHistorico, setIsEditHistorico] = useState(false);
  const [loteEdicao, setLoteEdicao] = useState(null);
  const [desabilitarCamposLancamento, setDesabilitarCamposLancamento] = useState(false);

  const [camposFixos, setCamposFixos] = useState<CamposFixos>({
    fixarCentroCustoCredito: false,
    fixarCentroCustoDebito: false,
    fixarContaCredito: false,
    fixarContaDebito: false,
    fixarData: false,
    fixarHistoricoPadrao: false,
    fixarHistoricoVariavel: false,
  });

  const [centroCustoDisabled, setCentroCustoDisabled] = useState({
    centroCustoCredito: true,
    centroCustoDebito: true,
  });

  useEffect(
    () => {
      ['historicos', 'lancamentos', 'idLancamentoAtual', 'idHistoricoAtual', 'indexLancamentoAtual', 'indexHistoricoAtual'].forEach(name =>
        formProps.register({ name })
      );

      setTimeout(() => {
        const refAtual = refToFormat(globalReducer.globalParameter.empresaMCG.refAtual);
        const refFinal = refToFormat(globalReducer.globalParameter.empresaMCG.refFinal);

        formProps.setValue('configuracao', { ...lancamentoConfig, label: lancamentoConfig.descricao });
        formProps.setValue('formaLancamento', lancamentoConfig.formaLancamento);
        formProps.setValue('referencia', `${refAtual}  ${refFinal}`);

        changeDataLancamento();

        if (selectedItem && !isEmpty(selectedItem.idLote)) {
          carregarLote();
        }
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  useEffect(
    () => {
      setCamposFixos(
        Object.keys(camposFixos)
          .map(key => ({ [key]: camposFixos[key] && lancamentoConfig[key] }))
          .reduce((result, it) => ({ ...result, ...it }))
      );
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [lancamentoConfig]
  );

  useEffect(
    () => {
      if (props.isFromVerifyError && !isEmpty(props.idMcgLancamentoLote)) {
        selectedItem.idLote = props.idMcgLancamentoLote;
        carregarLote();
      }
    }, // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  const carregarLote = () => {
    findById({ id: selectedItem.idLote }, CLASS_NAME, {
      thenFunction: (res: LancamentoLote) => {
        setLoteEdicao(res);

        formProps.setValue([{ lancamentos: res.lancamentos }, { numero: res.numero }]);

        if (res.dataInclusao && res.horaInclusao) {
          setLabelDataInclusao(`${res.dataInclusao} ${res.horaInclusao}`);
        }

        const values = formProps.getValues();
        if (res.lancamentos.length > 1 && values.formaLancamento === UM_DEBITO_UM_CREDITO.value) {
          const formaLancamento = VARIOS_DEBITOS_VARIOS_CREDITOS.value;
          formProps.setValue('formaLancamento', formaLancamento);
          setLancamentoConfig({
            ...lancamentoConfig,
            formaLancamento,
          });
        }

        renderUltimoLancamento();
      },
    });
  };

  const renderUltimoLancamento = () => {
    const values = formProps.getValues();
    if (values.lancamentos.length > 0) {
      renderLancamento(values.lancamentos[values.lancamentos.length - 1]);
    }
  };

  const renderLancamento = lancamento => {
    setIsEdit(true);
    formProps.setValue([
      { indexLancamentoAtual: lancamento.rowIndex || null },
      { idLancamentoAtual: lancamento.id },
      { planoContaCredito: lancamento.planoContaCredito },
      { planoContaDebito: lancamento.planoContaDebito },
      { numeroLancamento: lancamento.numero },
      { centroCustoCredito: lancamento.centroCustoCredito },
      { centroCustoDebito: lancamento.centroCustoDebito },
      { data: lancamento.data },
      { efeitoPatrimonioLiquido: lancamento.efeitoPatrimonioLiquido },
      { historicos: lancamento.historicos },
      { encerrado: lancamento.encerrado },
      { valor: lancamento.valor },
    ]);
    renderUltimoHistorico(lancamento);
    changeDataLancamento(false);

    setDesabilitarCamposLancamento(!lancamentoConfig.informarLancamentoEncerramento && lancamento.encerrado);

    setTimeout(() => {
      changeContaCredito(lancamento.planoContaCredito);
      changeContaDebito(lancamento.planoContaDebito);
    }, 200);
  };

  const renderUltimoHistorico = lancamento => {
    if (lancamento.historicos.length > 0) {
      renderHistorico({ ...lancamento.historicos[lancamento.historicos.length - 1], rowIndex: lancamento.historicos.length });
    }
  };

  const renderHistorico = historico => {
    setIsEditHistorico(true);
    formProps.setValue([
      { idHistoricoAtual: historico.id },
      { indexHistoricoAtual: historico.rowIndex || null },
      { historicoAtual: historico.historico },
      { historicoVariavel: historico.historicoVariavel },
    ]);
  };

  const changeConfig = () => {
    const values = formProps.getValues();
    if (values.configuracao) {
      setLancamentoConfig(values.configuracao);
      formProps.setValue('formaLancamento', values.configuracao.formaLancamento);
    }
  };

  const changeFormaLancamento = value => {
    setLancamentoConfig({
      ...lancamentoConfig,
      formaLancamento: value,
    });
  };

  const changeDataLancamento = (load = true) => {
    const values = formProps.getValues();
    setBloquearReferencia(values.data ? validatePeriodoConfig(values.data, globalReducer.globalParameter) : PERIODO_ANTERIOR.label);
    if (load) {
      loadSaldoDebito();
      loadSaldoCredito();
    }
  };

  const verificarEfeitoPatrimonio = () => {
    let isVisible = false;
    const digito = globalReducer.globalParameter.grupoPlanoConta.digitoPatrimonioLiquido;
    if (digito) {
      const values = formProps.getValues();
      if (values.planoContaDebito) {
        isVisible = values.planoContaDebito.conta.substr(0, digito.length) === digito;
      }
      if (!isVisible && values.planoContaCredito) {
        isVisible = values.planoContaCredito.conta.substr(0, digito.length) === digito;
      }
    }
    setPatrimonioLiquidoVisible(isVisible);
  };

  const loadSaldoDebito = (value = null, exibirAlerta: boolean = false) => {
    verificarEfeitoPatrimonio();
    const values = formProps.getValues();
    formProps.setValue('somaDebito', formatSaldo(0));
    if (values.planoContaDebito && values.data) {
      getSaldoDebito(
        {
          conta: value ? value.conta : values.planoContaDebito.conta,
          data: values.data,
          idGerEmpresa: globalReducer.globalParameter.empresa.id,
        },
        {
          thenFunction: res => {
            formProps.setValue('somaDebito', formatSaldo(res));
            if (exibirAlerta) {
              alertManager.emit(ACTION_EVENTS.SHOW, {
                message: 'Saldo Dbito: ' + formatSaldo(res),
                type: AlertType.INFORMATION,
              });
            }
          },
        }
      );
    }
  };

  const loadSaldoCredito = (value = null, exibirAlerta: boolean = false) => {
    verificarEfeitoPatrimonio();
    const values = formProps.getValues();
    formProps.setValue('somaCredito', formatSaldo(0));
    if (values.planoContaCredito && values.data) {
      getSaldoCredito(
        {
          conta: value ? value.conta : values.planoContaCredito.conta,
          data: values.data,
          idGerEmpresa: globalReducer.globalParameter.empresa.id,
        },
        {
          thenFunction: res => {
            formProps.setValue('somaCredito', formatSaldo(res));
            if (exibirAlerta) {
              alertManager.emit(ACTION_EVENTS.SHOW, {
                message: 'Saldo Crdito: ' + formatSaldo(res),
                type: AlertType.INFORMATION,
              });
            }
          },
        }
      );
    }
  };

  const validarCentroCusto = (value, centroCusto) => {
    const ativoPassivo =
      value &&
      value.conta.substr(0, 1) !== globalReducer.globalParameter.grupoPlanoConta.digitoAtivo &&
      value.conta.substr(0, 1) !== globalReducer.globalParameter.grupoPlanoConta.digitoPassivo;

    const utilizarAtivoPassivo = lancamentoConfig && lancamentoConfig.utilizarCentroCustoAtivoPassivo;

    if (value && (utilizarAtivoPassivo || ativoPassivo)) {
      setCentroCustoDisabled(p => ({ ...p, [centroCusto]: false }));
    } else {
      formProps.setValue(centroCusto, null);
      setCentroCustoDisabled(p => ({ ...p, [centroCusto]: true }));
    }
  };

  const validarContasReceitaDespesa = (value, campo) => {
    let msg = null;
    if (value) {
      if (campo === 'planoContaDebito') {
        if (value.conta && value.conta.charAt(0) === globalReducer.globalParameter.grupoPlanoConta.digitoReceita) {
          msg = 'Deseja realmente informar uma Conta de Receita para Dbito?';
        }
      } else if (campo === 'planoContaCredito') {
        if (value.conta && value.conta.charAt(0) === globalReducer.globalParameter.grupoPlanoConta.digitoDespesa) {
          msg = 'Deseja realmente informar uma Conta de Despesa para Crdito?';
        }
      }
    }

    if (msg) {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message: msg,
        onNoClick: () => formProps.setValue(campo, null),
        type: AlertType.WARNING_YES_NO,
      });
    }
  };

  const changeContaDebito = value => {
    loadSaldoDebito(value);
    validarCentroCusto(value, 'centroCustoDebito');
    if (lancamentoConfig && lancamentoConfig.validarLancReceitaDespesa) {
      validarContasReceitaDespesa(value, 'planoContaDebito');
    }
  };

  const changeContaCredito = value => {
    loadSaldoCredito(value);
    validarCentroCusto(value, 'centroCustoCredito');
    if (lancamentoConfig && lancamentoConfig.validarLancReceitaDespesa) {
      validarContasReceitaDespesa(value, 'planoContaCredito');
    }
  };

  const ajustarTelaViaLancamentoPadrao = lancamentos => {
    let countCreditos = 0;
    let countDebitos = 0;
    let variosHistoricos = 0;

    lancamentos.forEach(it => {
      if (it.planoContaCredito) {
        countCreditos++;
      }

      if (it.planoContaDebito) {
        countDebitos++;
      }

      if (it.historicos.length > 1) {
        variosHistoricos++;
      }
    });

    const config = { ...lancamentoConfig };

    config.historico = variosHistoricos > 0 ? VARIOS_LANCAMENTOS.value : LANCAMENTO_UNICO.value;

    if (countCreditos > 0 && countDebitos > 0) {
      if (countCreditos === 1 && countDebitos === 1 && lancamentos.length === 1) {
        config.formaLancamento = UM_DEBITO_UM_CREDITO.value;
      } else if (countCreditos === 1 && countDebitos > 1) {
        config.formaLancamento = UM_CREDITO_VARIOS_DEBITOS.value;
      } else if (countDebitos === 1 && countCreditos > 1) {
        config.formaLancamento = UM_DEBITO_VARIOS_CREDITOS.value;
      } else {
        config.formaLancamento = VARIOS_DEBITOS_VARIOS_CREDITOS.value;
      }
    }

    setLancamentoConfig(config);
    formProps.setValue('formaLancamento', config.formaLancamento);
  };

  const closeModal = () => setModalContent(null);

  const confirmModal = lancamentos => {
    const values = formProps.getValues();
    const listaFinal = [...values.lancamentos, ...lancamentos];

    formProps.setValue(
      'lancamentos',
      listaFinal.map((it, index) => ({ ...it, rowIndex: index + 1 }))
    );

    ajustarTelaViaLancamentoPadrao(listaFinal);
    renderUltimoLancamento();
  };

  const changeLancamentoPadrao = value => {
    if (value) {
      const values = formProps.getValues();
      const dataLancamento = values.data || formatDate(parseRefToDate(globalReducer.globalParameter.empresaMCG.refAtual, 1));
      setModalContent(
        <LancamentoPadraoModalView
          dataLancamento={dataLancamento}
          globalParameter={globalReducer.globalParameter}
          lancamentoConfig={lancamentoConfig}
          lotePadrao={value}
          onClose={closeModal}
          onConfirm={confirmModal}
        />
      );
    }
  };

  const showSaldoDebito = () => loadSaldoDebito(null, true);
  const showSaldoCredito = () => loadSaldoCredito(null, true);

  const onEditLancamento = lancamento => renderLancamento(lancamento);
  const onEditHistorico = historico => renderHistorico(historico);

  const onRemoveLancamento = lancamento => {
    alertManager.emit(ACTION_EVENTS.SHOW, {
      message: 'Deseja mesmo remover este lancamento?',
      onOkClick: () => {
        const values = formProps.getValues();
        formProps.setValue(
          'lancamentos',
          values.lancamentos.filter((it, index) => {
            const id = lancamento.id && it.id && lancamento.id === it.id;
            const idx = lancamento.rowIndex && lancamento.rowIndex - 1 === index;
            return !id && !idx;
          })
        );
      },
      type: AlertType.WARNING_YES_NO,
    });
  };

  const onRemoveHistorico = historico => {
    alertManager.emit(ACTION_EVENTS.SHOW, {
      message: 'Deseja mesmo remover este histrico?',
      onOkClick: () => {
        const values = formProps.getValues();
        formProps.setValue(
          'historicos',
          values.historicos.filter((it, index) => {
            const id = historico.id && it.id && historico.id === it.id;
            const idx = historico.rowIndex && historico.rowIndex - 1 === index;
            return !id && !idx;
          })
        );
      },
      type: AlertType.WARNING_YES_NO,
    });
  };

  const showHistoricoConcat = () => {
    const values = formProps.getValues();
    alertManager.emit(ACTION_EVENTS.SHOW, {
      message: concatenarHistorico(values.historicos),
      type: AlertType.INFORMATION,
    });
  };

  const submitHistorico = () => {
    const values = formProps.getValues();
    if (!isEmpty(values.historicoAtual) || !isEmpty(values.historicoVariavel)) {
      const historico = {
        codigo: values.historicoAtual ? values.historicoAtual.codigo : null,
        descricao: values.historicoAtual ? values.historicoAtual.descricao : null,
        historico: values.historicoAtual,
        historicoVariavel: values.historicoVariavel,
      };

      if (values.idHistoricoAtual || values.indexHistoricoAtual) {
        formProps.setValue(
          'historicos',
          values.historicos.map((it, index) => {
            let result = it;
            const id = values.idHistoricoAtual && values.idHistoricoAtual === it.id;
            const idx = values.indexHistoricoAtual && values.indexHistoricoAtual - 1 === index;
            if (id || idx) {
              result = { ...it, ...historico };
            }
            return result;
          })
        );
      } else {
        formProps.setValue('historicos', [...values.historicos, historico]);
      }

      setIsEditHistorico(false);

      formProps.setValue([{ idHistoricoAtual: null }, { indexHistoricoAtual: null }]);

      if (!values.fixarHistoricoPadrao) {
        formProps.setValue('historicoAtual', null);
      }

      if (!values.fixarHistoricoVariavel) {
        formProps.setValue('historicoVariavel', null);
      }
    }
  };

  const validateDebitoCredito = formData => {
    const erros = [];

    if (isEmpty(formData.planoContaDebito) && isEmpty(formData.planoContaCredito)) {
      erros.push('Contas: Nenhuma conta informada.');
    }

    if (
      !isEmpty(formData.planoContaDebito) &&
      !isEmpty(formData.planoContaCredito) &&
      formData.planoContaDebito.id === formData.planoContaCredito.id
    ) {
      erros.push('Contas: Dbito e Crdito no podem ser iguais.');
    }

    return erros;
  };

  const validateHistorico = formData => {
    const erros = [];

    if (formData.historicos.length === 0) {
      if (lancamentoConfig.utilizarHistoricoPadrao && lancamentoConfig.utilizarHistoricoVariavel) {
        if (isEmpty(formData.historicoAtual) && isEmpty(formData.historicoVariavel)) {
          erros.push('Histrico Padro/Varivel:  necessrio ter pelo menos um histrico para o lanamento.');
        }
      } else {
        if (lancamentoConfig.utilizarHistoricoPadrao && isEmpty(formData.historicoAtual)) {
          erros.push('Histrico Padro: Campo no pode estar em branco.');
        }

        if (lancamentoConfig.utilizarHistoricoVariavel && isEmpty(formData.historicoVariavel)) {
          erros.push('Histrico Varivel: Campo no pode estar em branco.');
        }
      }
    }

    return erros;
  };

  const validateCentroCusto = formData => {
    const erros = [];

    const ativo = globalReducer.globalParameter.grupoPlanoConta.digitoAtivo;
    const passivo = globalReducer.globalParameter.grupoPlanoConta.digitoPassivo;

    if (
      !isEmpty(formData.planoContaDebito) &&
      formData.planoContaDebito.conta.charAt(0) !== ativo &&
      formData.planoContaDebito.conta.charAt(0) !== passivo
    ) {
      if (isEmpty(formData.centroCustoDebito)) {
        erros.push('Centro de Custo Dbito no pode estar em branco.');
      }
    }

    if (
      !isEmpty(formData.planoContaCredito) &&
      formData.planoContaCredito.conta.charAt(0) !== ativo &&
      formData.planoContaCredito.conta.charAt(0) !== passivo
    ) {
      if (isEmpty(formData.centroCustoCredito)) {
        erros.push('Centro de Custo Credito no pode estar em branco.');
      }
    }

    return erros;
  };

  const validatePartidaDobrada = formData => {
    const erros = [];

    if (formData.lancamentos.length > 1 && (isEmpty(selectedItem.id) || !formData.encerrado)) {
      const checkIndexId = (it, index) => {
        let result = false;
        if (formData.idLancamentoAtual) {
          result = formData.idLancamentoAtual !== it.id;
        } else if (formData.indexLancamentoAtual) {
          result = formData.indexLancamentoAtual - 1 !== index;
        }
        return result;
      };
      const partidaDobrada = formData.lancamentos.some(
        (it, index) => !isEmpty(it.planoContaDebito) && !isEmpty(it.planoContaCredito) && checkIndexId(it, index)
      );
      if (partidaDobrada) {
        erros.push('O lanamento de partida dobrada deve ser o nico do lote.');
      }
    }

    return erros;
  };

  const validateLancamento = () => {
    const formData = formProps.getValues();
    const erros = [];

    if (isEmpty(formData.data)) {
      erros.push('Data de lanamento  de preenchimento obrigatrio');
    } else {
      const bloqueio = validatePeriodoConfig(formData.data, globalReducer.globalParameter);
      if (bloqueio === null || bloqueio === PERIODO_ANTERIOR.label) {
        if (parseInt(formatDate(toDate(formData.data), 'YYYY'), 10) < 1000) {
          erros.push('Lanamento deve ser posterior ao ano 1000');
        }
      } else {
        erros.push('Lanamento ' + bloqueio);
      }
    }

    if (isEmpty(formData.valor)) {
      erros.push('Valor deve ser maior que 0');
    }

    erros.push(...validateDebitoCredito(formData));
    erros.push(...validateHistorico(formData));
    erros.push(...validateCentroCusto(formData));
    erros.push(...validatePartidaDobrada(formData));

    if (patrimonioLiquidoVisible && isEmpty(formData.efeitoPatrimonioLiquido)) {
      erros.push('Motivo de Modificao do Patrimnio Lquido - Campo no pode estar em branco');
    }

    const dataBase = formData.lancamentos.length > 0 ? formData.lancamentos[0].data : formData.data;

    if (compare(dataBase, formData.data) !== 0) {
      erros.push('As datas dos lanamentos de mesmo lote no podem ser diferentes.');
    }

    return erros;
  };

  const submitLancamento = () => {
    let result = true;

    const erros = validateLancamento();

    if (erros.length > 0) {
      result = false;
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message: <ErroList erros={erros} />,
        type: AlertType.WARNING,
      });
    } else {
      submitHistorico();

      const values = formProps.getValues();
      let lancamentos = values.lancamentos;

      const numero = parseFloat(values.numeroLancamento);

      const lancamento: Lancamento = {
        centroCustoCredito: values.centroCustoCredito,
        centroCustoDebito: values.centroCustoDebito,
        data: values.data,
        efeitoPatrimonioLiquido: patrimonioLiquidoVisible
          ? EnumUtil.find(EFEITO_PATRIMONIO_LIQUIDO_ENUM_VALUES, values.efeitoPatrimonioLiquido).extraFields.intValue
          : NAO_INTERFERE.extraFields.intValue,
        empresa: globalReducer.globalParameter.empresa,
        empresaOrigem: globalReducer.globalParameter.empresa,
        encerrado: values.encerrado,
        historicos: values.historicos,
        numero: isNaN(numero) ? null : numero,
        origem: CONTABILIDADE.extraFields.intValue,
        origemLancamento: CONTABILIDADE.extraFields.intValue,
        planoContaCredito: values.planoContaCredito,
        planoContaDebito: values.planoContaDebito,
        valor: values.valor,
      };

      if (values.idLancamentoAtual || values.indexLancamentoAtual) {
        lancamentos = lancamentos.map((it, index) => {
          const id = values.idLancamentoAtual && values.idLancamentoAtual === it.id;
          const idx = values.indexLancamentoAtual && values.indexLancamentoAtual - 1 === index;
          let res = it;
          if (id || idx) {
            res = { ...it, ...lancamento };
          }
          return res;
        });
      } else {
        lancamento.lote = { numero: values.numero };
        lancamentos.push(lancamento);
      }

      setIsEdit(false);

      formProps.setValue([
        { lancamentos },
        { numeroLancamento: null },
        { lancamentoPadrao: null },
        { efeitoPatrimonioLiquido: null },
        { historicos: [] },
        { valor: null },
        { encerrado: false },
        { idLancamentoAtual: null },
        { indexLancamentoAtual: null },
        { idHistoricoAtual: null },
        { indexHistoricoAtual: null },
      ]);

      const zerarCampoFixo = (idCampo, idCheck) => {
        if (!values[idCheck]) {
          formProps.setValue(idCampo, null);
        }
      };

      zerarCampoFixo('data', 'fixarData');
      zerarCampoFixo('planoContaDebito', 'fixarContaDebito');
      zerarCampoFixo('centroCustoDebito', 'fixarCentroCustoDebito');
      zerarCampoFixo('planoContaCredito', 'fixarContaCredito');
      zerarCampoFixo('centroCustoCredito', 'fixarCentroCustoCredito');
      zerarCampoFixo('historicoAtual', 'fixarHistoricoPadrao');
      zerarCampoFixo('historicoVariavel', 'fixarHistoricoVariavel');

      changeDataLancamento();
    }

    return result;
  };

  const ajustarHistoricoPersist = (historico, index) => {
    historico.indice = index;
    return historico;
  };

  const ajustarLancamentoPersist = (lancamento, index) => {
    lancamento.indice = index;
    lancamento.historicos = lancamento.historicos.map(ajustarHistoricoPersist);
    delete lancamento.lote;
    return lancamento;
  };

  const finalizarBeforePersist = (formData, done) => {
    const lote: LancamentoLote = {
      idGerEmpresa: globalReducer.globalParameter.empresa.id,
      lancamentos: formData.lancamentos.map(ajustarLancamentoPersist),
    };

    if (loteEdicao) {
      lote.id = loteEdicao.id;
      lote.numero = loteEdicao.numero;
    }

    done(lote);
  };

  const beforePersist = (_, done) => {
    let ok = true;

    if (formProps.getValues().valor > 0) {
      ok = submitLancamento();
    }

    const formData = formProps.getValues();

    if (ok) {
      if (formData.lancamentos.length === 0) {
        alertManager.emit(ACTION_EVENTS.SHOW, {
          message: 'Lista de lanamentos no pode estar vazia',
          type: AlertType.INFORMATION,
        });
        ok = false;
      }
    }

    if (ok) {
      let debitos = 0;
      let creditos = 0;

      formData.lancamentos.forEach(lancamento => {
        if (lancamento.planoContaDebito) {
          debitos += lancamento.valor;
        }
        if (lancamento.planoContaCredito) {
          creditos += lancamento.valor;
        }
      });

      if (debitos === creditos) {
        finalizarBeforePersist(formData, done);
      } else {
        if (lancamentoConfig.validacaoLancPartidaSimples === NAO.value) {
          alertManager.emit(ACTION_EVENTS.SHOW, {
            message: 'A soma dos valores  Dbito diferem dos valores  Crdito.',
            type: AlertType.ERROR,
          });
        } else if (lancamentoConfig.validacaoLancPartidaSimples === SIM.value) {
          finalizarBeforePersist(formData, done);
        } else {
          alertManager.emit(ACTION_EVENTS.SHOW, {
            message: 'A soma dos valores  Dbito diferem dos valores  Crdito. Deseja continuar?',
            onOkClick: () => {
              finalizarBeforePersist(formData, done);
            },
            type: AlertType.WARNING_YES_NO,
          });
        }
      }
    }

    return { async: true };
  };

  const atualizarCalculosIRPJeCSLL = referencia =>
    new Promise(resolve => {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message:
          'O lanamento interfere no clculo de IRPJ ou CSLL j calculado no sistema.  possvel que o procedimento de recalculo ' +
          'possa ser demorado. Deseja recalcular o IRPJ e CSLL desse ano j apurado no sistema?',
        onNoClick: () => resolve(),
        onOkClick: () => {
          atualizaCalculoIrpjCsll(
            {
              empresaMCG: globalReducer.globalParameter.empresaMCG,
              referencia,
            },
            {
              thenFunction: () => {
                resolve();
              },
            }
          );
        },
        type: AlertType.WARNING_YES_NO,
      });
    });

  const afterPersist = (lote: LancamentoLote, done) => {
    const { empresa, empresaMLF } = globalReducer.globalParameter;
    const lucroReal = EnumUtil.contains(TRIBUTACAO_LUCRO_REAL_ENUM_VALUES, empresa.tipoTributacao);
    const irpj = empresaMLF.configIRPJ && empresaMLF.configIRPJ.tipoApuracao === CONTABIL.value;
    const csll = empresaMLF.configCSLL && empresaMLF.configCSLL.tipoApuracao === CONTABIL.value;

    if (lucroReal && irpj && csll) {
      const contas = [];

      lote.lancamentos.forEach(it => {
        if (it.planoContaDebito) {
          contas.push(it.planoContaDebito.conta);
        }
        if (it.planoContaCredito) {
          contas.push(it.planoContaCredito.conta);
        }
      });

      if (contas.length > 0) {
        const referencia = formatRef(toDate(lote.lancamentos[0].data));
        isAlteraCalculoIRPJorCSLL(
          {
            empresaMCG: globalReducer.globalParameter.empresaMFP,
            nomeContaCreditoDebito: contas,
            referencia,
          },
          {
            thenFunction: res => {
              if (res) {
                atualizarCalculosIRPJeCSLL(referencia).then(() => done());
              } else {
                done();
              }
            },
          }
        );
      } else {
        done();
      }
    } else {
      done();
    }
    return { async: true };
  };

  const lockCheckbox = (id, reference, label = null) => {
    const changeLockCheckbox = () => {
      const values = formProps.getValues();
      setCamposFixos(c => {
        c[id] = values[id];
        return { ...c };
      });
    };

    return (
      lancamentoConfig &&
      lancamentoConfig[id] && (
        <>
          <div id={'box-' + id} className="lock-checkbox">
            <Checkbox id={id} formProps={formProps} onChange={changeLockCheckbox} />
            <FontAwesomeIcon icon={camposFixos[id] ? 'lock' : 'lock-open'} />
          </div>
          <UncontrolledTooltip placement="bottom" target={'box-' + id}>
            Fixar {label}
          </UncontrolledTooltip>
        </>
      )
    );
  };

  return (
    <WindowComponent title="Manuteno de Lanamentos" wikiId={2302}>
      <EditForm
        formProps={formProps}
        editViewProps={props}
        dtoClassName={CLASS_NAME}
        beforePersist={beforePersist}
        afterPersist={afterPersist}
        hideSubmitButton={desabilitarCamposLancamento}
      >
        {modalContent}
        {desabilitarCamposLancamento && (
          <p style={{ color: CALIMA_DANGER }} className="text-center">
            Ateno: Lanamento Automtico de Encerramento de Resultado. Esse lanamento no pode ser alterado.
          </p>
        )}
        <Row>
          <Col>
            <InputGroupInline
              formProps={formProps}
              id="formaLancamento"
              inputDropDownProps={{ optionsProvider: LANCAMENTO_MCG_FORMA_ENUM_VALUES }}
              isPrependLabel={true}
              label="Frmula"
              placeholder="Informe o Tipo de Frmula"
              type={InputType.DROP_DOWN}
              onChange={changeFormaLancamento}
            />
          </Col>
          <Col>
            <LancamentoMCGConfigIS id="configuracao" formProps={formProps} label="Configurao" onChange={changeConfig} />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={formProps}
              isPrependLabel={true}
              id="numero"
              label="N Lote"
              tooltipProps={{ message: 'Preenchimento Opcional - Codificao Automtica' }}
              disabled={desabilitarCamposLancamento}
            />
          </Col>
          <Col md={4}>
            <InputGroupInline
              formProps={formProps}
              isPrependLabel={true}
              id="numeroLancamento"
              label="N Lanamento"
              tooltipProps={{ message: 'Preenchimento Opcional - Codificao Automtica' }}
              disabled={desabilitarCamposLancamento}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline
              formProps={formProps}
              isPrependLabel={true}
              id="data"
              label="Data do Lanamento"
              type={InputType.DATE}
              disabled={camposFixos.fixarData || desabilitarCamposLancamento}
              onBlur={changeDataLancamento}
            />
          </Col>
          {lockCheckbox('fixarData', 'data', 'Data de Lanamento')}
          <Col className={cx({ bloqueado: !!bloquearReferencia })}>
            <InputGroupInline
              formProps={formProps}
              isPrependLabel={true}
              id="referencia"
              label="Referencia"
              disabled={true}
              tooltipProps={bloquearReferencia && { message: 'Lanamento ' + bloquearReferencia }}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <LancamentoPadraoIS
              id="lancamentoPadrao"
              formProps={formProps}
              label="Lanamento Padro"
              onChange={changeLancamentoPadrao}
              disabled={desabilitarCamposLancamento}
            />
          </Col>
        </Row>
        <Box title="Dbito">
          <Row>
            <Col>
              <PlanoContaIS
                id="planoContaDebito"
                formProps={formProps}
                label="Conta"
                disabled={camposFixos.fixarContaDebito || desabilitarCamposLancamento}
                onChange={changeContaDebito}
              />
            </Col>
            {lockCheckbox('fixarContaDebito', 'planoContaDebito', 'Conta de Dbito')}
            {lancamentoConfig && lancamentoConfig.exibirSaldoContas ? (
              <div style={{ paddingRight: 20, width: 200, marginLeft: 15 }}>
                <Input innerRef={formProps.register} name="somaDebito" id="somaDebito" disabled={true} />
              </div>
            ) : (
              <div style={{ paddingRight: 20, marginLeft: 15 }}>
                <Button id="somaDebito" style={{ width: 80 }} onClick={showSaldoDebito}>
                  Saldo
                </Button>
              </div>
            )}
          </Row>
          {lancamentoConfig && lancamentoConfig.utilizarCentroCusto && (
            <Row>
              <Col>
                <CentroCustoIS
                  id="centroCustoDebito"
                  formProps={formProps}
                  label="Centro de Custo"
                  disabled={camposFixos.fixarCentroCustoDebito || centroCustoDisabled.centroCustoDebito || desabilitarCamposLancamento}
                />
              </Col>
              {lockCheckbox('fixarCentroCustoDebito', 'centroCustoDebito', 'Centro de Custo')}
            </Row>
          )}
        </Box>
        <Box title="Crdito">
          <Row>
            <Col>
              <PlanoContaIS
                id="planoContaCredito"
                formProps={formProps}
                label="Conta"
                disabled={camposFixos.fixarContaCredito || desabilitarCamposLancamento}
                onChange={changeContaCredito}
              />
            </Col>
            {lockCheckbox('fixarContaCredito', 'planoContaCredito', 'Conta de Crdito')}
            {lancamentoConfig && lancamentoConfig.exibirSaldoContas ? (
              <div style={{ paddingRight: 20, width: 200, marginLeft: 15 }}>
                <Input innerRef={formProps.register} name="somaCredito" id="somaCredito" disabled={true} />
              </div>
            ) : (
              <div style={{ paddingRight: 20, marginLeft: 15 }}>
                <Button id="somaCredito" style={{ width: 80 }} onClick={showSaldoCredito}>
                  Saldo
                </Button>
              </div>
            )}
          </Row>
          {lancamentoConfig && lancamentoConfig.utilizarCentroCusto && (
            <Row>
              <Col>
                <CentroCustoIS
                  id="centroCustoCredito"
                  formProps={formProps}
                  label="Centro de Custo"
                  disabled={camposFixos.fixarCentroCustoCredito || centroCustoDisabled.centroCustoCredito || desabilitarCamposLancamento}
                />
              </Col>
              {lockCheckbox('fixarCentroCustoCredito', 'centroCustoCredito', 'Centro de Custo')}
            </Row>
          )}
        </Box>
        {patrimonioLiquidoVisible && (
          <Row>
            <Col>
              <InputGroupInline
                formProps={formProps}
                id="efeitoPatrimonioLiquido"
                inputDropDownProps={{ optionsProvider: EFEITO_PATRIMONIO_LIQUIDO_ENUM_VALUES }}
                isPrependLabel={true}
                label="Motivo de Modificao do Patrimnio Lquido"
                type={InputType.DROP_DOWN}
              />
            </Col>
          </Row>
        )}
        <Row>
          <Col>
            <HistoricoIS
              id="historicoAtual"
              formProps={formProps}
              label="Histrico Padro"
              disabled={camposFixos.fixarHistoricoPadrao || desabilitarCamposLancamento}
            />
          </Col>
          {lockCheckbox('fixarHistoricoPadrao', 'historicoAtual', 'Histrico')}
        </Row>
        <Row>
          <Col>
            <Card className="historico-variavel">
              <CardHeader className="card-header pr-2">
                <div className="card-header--title">Histrico Varivel</div>
                <div className="card-header--actions">
                  {lancamentoConfig && lancamentoConfig.historico === VARIOS_LANCAMENTOS.value && (
                    <>
                      <Button
                        id="incluirHistorico"
                        size="sm"
                        color="link"
                        className={isEditHistorico ? 'text-primary' : 'text-success'}
                        onClick={submitHistorico}
                      >
                        <FontAwesomeIcon icon={isEditHistorico ? 'save' : 'plus'} />
                      </Button>
                      <UncontrolledTooltip placement="bottom" target="incluirHistorico">
                        {isEditHistorico ? 'Salvar Alteraes' : 'Incluir Histrico'}
                      </UncontrolledTooltip>
                    </>
                  )}
                  <Button id="exibirHistorico" size="sm" color="link" className="text-warning" onClick={showHistoricoConcat}>
                    <FontAwesomeIcon icon="link" />
                  </Button>
                  <UncontrolledTooltip placement="bottom" target="exibirHistorico">
                    Exibir histrico concatenado
                  </UncontrolledTooltip>
                  {lockCheckbox('fixarHistoricoVariavel', 'historicoVariavel', 'Histrico Varivel')}
                </div>
              </CardHeader>
              <CardBody>
                <Input
                  type="textarea"
                  name="historicoVariavel"
                  innerRef={formProps.register}
                  disabled={camposFixos.fixarHistoricoVariavel || desabilitarCamposLancamento}
                  placeholder="Histrico Varivel"
                />
              </CardBody>
            </Card>
          </Col>
        </Row>
        {lancamentoConfig && lancamentoConfig.historico === VARIOS_LANCAMENTOS.value && (
          <Row>
            <Col>
              <HistoricoVariavelDataGrid
                formProps={formProps}
                onEdit={onEditHistorico}
                onRemove={onRemoveHistorico}
                disabled={desabilitarCamposLancamento}
              />
            </Col>
          </Row>
        )}
        <Row>
          <Col md={4}>
            <InputGroupInline
              formProps={formProps}
              isPrependLabel={true}
              id="valor"
              label="Valor"
              type={InputType.MONEY}
              disabled={desabilitarCamposLancamento}
            />
          </Col>
          {lancamentoConfig && lancamentoConfig.formaLancamento !== UM_DEBITO_UM_CREDITO.value && (
            <div>
              <Button color={isEdit ? 'primary' : 'success'} onClick={submitLancamento} disabled={desabilitarCamposLancamento}>
                {isEdit ? 'Alterar' : 'Incluir'}
              </Button>
            </div>
          )}
          <Col style={{ paddingTop: 10 }} className="text-right">
            {labelDataInclusao && (
              <p className="d-inline-block pr-4" style={{ color: CALIMA_DANGER }}>
                Registro includo em {labelDataInclusao}
              </p>
            )}
            <Checkbox id="encerrado" formProps={formProps} label="Conta de Encerramento" />
          </Col>
        </Row>
        {lancamentoConfig && lancamentoConfig.formaLancamento !== UM_DEBITO_UM_CREDITO.value && (
          <Row>
            <Col>
              <LancamentoItensDataGrid
                formProps={formProps}
                lancamentoConfig={lancamentoConfig}
                empresaMCG={globalReducer.globalParameter.empresaMCG}
                onEdit={onEditLancamento}
                onRemove={onRemoveLancamento}
                disabled={desabilitarCamposLancamento}
                numeroLote={(loteEdicao && loteEdicao.numero) || null}
              />
            </Col>
          </Row>
        )}
      </EditForm>
    </WindowComponent>
  );
};

export default LancamentoSaldoEditView;
