import React, { FC } from 'react';
import { InputGroupInline, InputProps, InputType } from 'summer';
import { CLASS_NAME } from '../../../../models/dtos/mcg/lotePadrao';

const LancamentoPadraoIS: FC<InputProps> = props => {
  return (
    <InputGroupInline
      {...props}
      isPrependLabel={true}
      inputSearchProps={{
        ...props.inputSearchProps,
        columns: [
          {
            dataField: 'codigo',
            text: 'Cdigo',
          },
          {
            dataField: 'descricao',
            text: 'Descrio',
          },
        ],
        dtoClassName: CLASS_NAME,
        searchViewId: 'LancamentoPadraoSearchView',
      }}
      label={props.label}
      placeholder={props.placeholder}
      type={InputType.INPUT_SEARCH}
    />
  );
};

export default LancamentoPadraoIS;
