import React, { FC, useEffect, useState } from 'react';
import { useForm } from 'react-hook-form';
import { Badge, Col, Modal, ModalBody, ModalFooter, ModalHeader, Row } from 'reactstrap';
import { ACTION_EVENTS, alertManager, AlertType, Box, ErroList, InputGroupInline, InputType, isEmpty, ProcessBar } from 'summer';
import { GlobalParameter } from '../../../../models/dtos/ger/globalParameter';
import { LancamentoMCGConfig } from '../../../../models/dtos/mcg/lancamentoMCGConfig';
import { LotePadrao } from '../../../../models/dtos/mcg/lotePadrao';
import { CODIGO_REDUZIDO } from '../../../../models/enumerated/ger/planoContaGERTipoLancamentoContaEnum';
import { concatenarHistorico } from '../../../../utilities/mcg.util';

interface LancamentoPadraoModalViewProps {
  dataLancamento: string;
  globalParameter: GlobalParameter;
  lancamentoConfig: LancamentoMCGConfig;
  lotePadrao: LotePadrao;
  onClose: () => void;
  onConfirm: (lancamentos) => void;
}

interface FormFields {
  centroCustoCredito?: string;
  centroCustoDebito?: string;
  credito?: string;
  debito?: string;
  dtLancamento?: string;
  historico?: string;
  valor?: number;
}

const LancamentoPadraoModalView: FC<LancamentoPadraoModalViewProps> = props => {
  const formProps = useForm<FormFields>({
    defaultValues: {
      dtLancamento: props.dataLancamento,
    },
    mode: 'onBlur',
  });

  const [lancamentos, setLancamentos] = useState([]);
  const [indexAtual, setIndexAtual] = useState(-1);

  useEffect(
    () => {
      setTimeout(() => {
        setLancamento(0);
      }, 700);
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    []
  );

  useEffect(
    () => {
      const nextIndex = indexAtual + 1;
      if (nextIndex < props.lotePadrao.lotePadraoLancamento.length) {
        setLancamento(nextIndex);
      } else {
        props.onConfirm(lancamentos);
        props.onClose();
      }
    },
    // eslint-disable-next-line react-hooks/exhaustive-deps
    [lancamentos]
  );

  const getDescConta = conta => {
    let result = '';
    const reduzido = props.globalParameter.empresaMCG.tipoLancamentoConta === CODIGO_REDUZIDO.value;
    if (conta) {
      result = reduzido ? conta.codigoReduzido : conta.conta;
    }
    return result;
  };

  const setLancamento = index => {
    setIndexAtual(index);
    const lancamento = props.lotePadrao.lotePadraoLancamento[index];
    formProps.setValue([
      { debito: getDescConta(lancamento.planoContaDebito) },
      { credito: getDescConta(lancamento.planoContaCredito) },
      { centroCustoDebito: lancamento.centroCustoDebito && lancamento.centroCustoDebito.descricao },
      { centroCustoCredito: lancamento.centroCustoCredito && lancamento.centroCustoCredito.descricao },
      { historico: concatenarHistorico(lancamento.historico) },
      { valor: null },
    ]);
  };

  const onClose = () => {
    if (lancamentos.length > 0) {
      props.onConfirm(lancamentos);
    }
    props.onClose();
  };

  const validateExtras = formData => {
    const erros = [];

    if (isEmpty(formData.dtLancamento)) {
      erros.push('Data de Lanamento: Campo no pode estar em branco');
    }

    const valor = parseFloat(formData.valor);
    if (isNaN(valor) || valor <= 0) {
      erros.push('Valor: Valor deve ser maior que 0');
    }

    return erros;
  };

  const onSubmit = () => {
    const values = formProps.getValues();
    const erros = validateExtras(values);
    if (erros.length > 0) {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message: <ErroList erros={erros} />,
        type: AlertType.ERROR,
      });
    } else {
      const lancamento = props.lotePadrao.lotePadraoLancamento[indexAtual];
      setLancamentos(l => [
        ...l,
        {
          centroCustoCredito: lancamento.centroCustoCredito,
          centroCustoDebito: lancamento.centroCustoDebito,
          data: values.dtLancamento,
          historicos: lancamento.historico.map(it => ({
            codigo: it.historico ? it.historico.codigo : null,
            descricao: it.historico ? it.historico.descricao : null,
            historico: it.historico || null,
            historicoVariavel: it.historicoVariavel,
          })),
          planoContaCredito: lancamento.planoContaCredito,
          planoContaDebito: lancamento.planoContaDebito,
          valor: values.valor,
        },
      ]);
    }
  };

  return (
    <Modal isOpen={true} backdrop="static" centered={true} toggle={props.onClose} size="lg">
      <ModalHeader toggle={props.onClose}>
        <Badge color="primary" pill={true} className="mr-2 mt-1 align-top">
          {indexAtual + 1} / {props.lotePadrao.lotePadraoLancamento.length}
        </Badge>
        Lanamento Padro
      </ModalHeader>
      <ModalBody>
        <Row>
          <Col>
            <InputGroupInline
              formProps={{ ...formProps, validation: { required: true } }}
              isPrependLabel={true}
              id="dtLancamento"
              label="Data"
              type={InputType.DATE}
            />
          </Col>
        </Row>
        <Box title="Dbito">
          <Row>
            <Col>
              <InputGroupInline formProps={formProps} isPrependLabel={true} id="debito" label="Conta" disabled={true} />
            </Col>
          </Row>
          {props.lancamentoConfig.utilizarCentroCusto && (
            <Row>
              <Col>
                <InputGroupInline formProps={formProps} isPrependLabel={true} id="centroCustoDebito" label="Centro Custo" disabled={true} />
              </Col>
            </Row>
          )}
        </Box>
        <Box title="Crdito">
          <Row>
            <Col>
              <InputGroupInline formProps={formProps} isPrependLabel={true} id="credito" label="Conta" disabled={true} />
            </Col>
          </Row>
          {props.lancamentoConfig.utilizarCentroCusto && (
            <Row>
              <Col>
                <InputGroupInline formProps={formProps} isPrependLabel={true} id="centroCustoCredito" label="Centro Custo" disabled={true} />
              </Col>
            </Row>
          )}
        </Box>
        <Row>
          <Col>
            <InputGroupInline
              formProps={formProps}
              isPrependLabel={true}
              id="historico"
              label="Histrico"
              type={InputType.TEXT_AREA}
              disabled={true}
            />
          </Col>
        </Row>
        <Row>
          <Col>
            <InputGroupInline formProps={formProps} isPrependLabel={true} id="valor" label="Valor" type={InputType.MONEY} />
          </Col>
        </Row>
      </ModalBody>
      <ModalFooter>
        <ProcessBar handleSubmit={formProps.handleSubmit(onSubmit)} handleClose={onClose} />
      </ModalFooter>
    </Modal>
  );
};

export default LancamentoPadraoModalView;
