import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { FC, useState } from 'react';
import BootstrapTable from 'react-bootstrap-table-next';
import ToolkitProvider from 'react-bootstrap-table2-toolkit';
import { Button, Card, CardBody, CardHeader, UncontrolledTooltip } from 'reactstrap';
import { ACTION_EVENTS, alertManager, AlertType, decimalFormatter, UseFormProps } from 'summer';
import { EmpresaMCG } from '../../../../models/dtos/mcg/empresaMCG';
import { Lancamento } from '../../../../models/dtos/mcg/lancamento';
import { LancamentoMCGConfig } from '../../../../models/dtos/mcg/lancamentoMCGConfig';
import { CODIGO_REDUZIDO as CODIGO_REDUZIDO_CENTRO } from '../../../../models/enumerated/ger/centroCustoGERTipoLancamentoCentroCustoEnum';
import { CODIGO_REDUZIDO as CODIGO_REDUZIDO_PLANO } from '../../../../models/enumerated/ger/planoContaGERTipoLancamentoContaEnum';
import { concatenarHistorico } from '../../../../utilities/mcg.util';

import 'react-bootstrap-table-next/dist/react-bootstrap-table2.min.css';
import 'react-bootstrap-table2-toolkit/dist/react-bootstrap-table2-toolkit.min.css';
import './LancamentoItensDataGrid.css';

interface LancamentoItensDataGridProps {
  lancamentoConfig: LancamentoMCGConfig;
  formProps: UseFormProps;
  empresaMCG: EmpresaMCG;
  onEdit: (lancamento: Lancamento & { rowIndex: number }) => void;
  onRemove: (lancamento: Lancamento & { rowIndex: number }) => void;
  disabled: boolean;
  numeroLote: number;
}

const LancamentoItensDataGrid: FC<LancamentoItensDataGridProps> = props => {
  const [selecionado, setSelecionado] = useState(null);

  const contaDebitoOuCRFormatter = (col, row) => {
    let result = '';
    if (row.planoContaDebito) {
      result =
        props.empresaMCG.tipoLancamentoConta === CODIGO_REDUZIDO_PLANO.value ? row.planoContaDebito.codigoReduzido : row.planoContaDebito.conta;
    }
    return result;
  };

  const contaCreditoOuCRFormatter = (col, row) => {
    let result = '';
    if (row.planoContaCredito) {
      result =
        props.empresaMCG.tipoLancamentoConta === CODIGO_REDUZIDO_PLANO.value ? row.planoContaCredito.codigoReduzido : row.planoContaCredito.conta;
    }
    return result;
  };

  const centroCustoCreditoOuCRFormatter = (col, row) => {
    let result = '';
    if (row.centroCustoCredito) {
      result =
        props.empresaMCG.tipoLancamentoConta === CODIGO_REDUZIDO_CENTRO.value ? row.centroCustoCredito.codigoReduzido : row.centroCustoCredito.codigo;
    }
    return result;
  };

  const centroCustoDebitoOuCRFormatter = (col, row) => {
    let result = '';
    if (row.centroCustoDebito) {
      result =
        props.empresaMCG.tipoLancamentoConta === CODIGO_REDUZIDO_CENTRO.value ? row.centroCustoDebito.codigoReduzido : row.centroCustoDebito.codigo;
    }
    return result;
  };

  const columns = [
    {
      dataField: 'data',
      text: 'Data do Lanamento',
    },
    {
      dataField: 'lote',
      formatter: () => props.numeroLote,
      text: 'N Lote',
    },
    {
      dataField: 'numero',
      text: 'N Lanamento',
    },
    {
      dataField: 'contaDebitoOuCR',
      formatter: contaDebitoOuCRFormatter,
      text: 'Conta de Dbito',
    },
    {
      dataField: 'centroCustoDebitoOuCR',
      formatter: centroCustoDebitoOuCRFormatter,
      hidden: !props.lancamentoConfig || props.lancamentoConfig.utilizarCentroCusto === false,
      text: 'Centro de Custo Dbito',
    },
    {
      dataField: 'contaCreditoOuCR',
      formatter: contaCreditoOuCRFormatter,
      text: 'Conta de Crdito',
    },
    {
      dataField: 'centroCustoCreditoOuCR',
      formatter: centroCustoCreditoOuCRFormatter,
      hidden: !props.lancamentoConfig || props.lancamentoConfig.utilizarCentroCusto === false,
      text: 'Centro de Custo Crdito',
    },
    {
      dataField: 'valor',
      formatter: decimalFormatter,
      text: 'Valor',
    },
    {
      dataField: 'historicos',
      formatter: concatenarHistorico,
      text: 'Histrico',
    },
  ];

  const info = { credito: 0, debito: 0, diferenca: 0 };

  const lancamentos = props.formProps.watch('lancamentos').map((it, rowIndex) => ({ ...it, rowIndex }));

  lancamentos.forEach(it => {
    if (it.planoContaCredito) {
      info.credito += it.valor;
    }
    if (it.planoContaDebito) {
      info.debito += it.valor;
    }
  });

  const onSelect = (row, _, index) => setSelecionado({ ...row, rowIndex: index + 1 });

  const checkSelect = () => {
    const result = selecionado !== null;
    if (result === false) {
      alertManager.emit(ACTION_EVENTS.SHOW, {
        message: 'Selecione um registro',
        type: AlertType.INFORMATION,
      });
    }
    return result;
  };

  const onEdit = () => {
    if (checkSelect()) {
      props.onEdit(selecionado);
    }
  };

  const onRemove = () => {
    if (checkSelect()) {
      props.onRemove(selecionado);
    }
  };

  return (
    <Card className="card-box grid-lancamentos">
      <CardHeader className="card-header pr-2">
        <div className="card-header--title">Lanamentos</div>
        <div className="card-header--actions">
          <Button id="editLancButton" size="sm" color="link" className="text-warning" onClick={onEdit} disabled={props.disabled}>
            <FontAwesomeIcon icon="pen" />
          </Button>
          <UncontrolledTooltip target="editLancButton">Editar Lanamento Selecionado</UncontrolledTooltip>
          <Button id="removeLancButton" size="sm" color="link" className="text-danger" onClick={onRemove} disabled={props.disabled}>
            <FontAwesomeIcon icon="trash" />
          </Button>
          <UncontrolledTooltip target="removeLancButton">Excluir Lanamento Selecionado</UncontrolledTooltip>
        </div>
      </CardHeader>
      <CardBody>
        <ToolkitProvider keyField="rowIndex" columns={columns} data={lancamentos} bootstrap4={true}>
          {toolkitprops => [
            <BootstrapTable
              {...toolkitprops.baseProps}
              striped={true}
              bordered={true}
              hover={true}
              responsive={true}
              condensed={true}
              key="histVariavelDados"
              selectRow={{
                clickToSelect: true,
                mode: 'radio',
                onSelect,
              }}
              rowEvents={{
                onDoubleClick: () => onEdit(),
              }}
            />,
            <div key="divFotterKey" className="d-flex justify-content-between">
              <span>Registros: {lancamentos.length}</span>
              <div className="footer">
                <span>Dbito: {decimalFormatter(info.debito, null)}</span>
                <span>Crdito: {decimalFormatter(info.credito, null)}</span>
                <span>Diferena: {decimalFormatter(info.credito - info.credito, null)}</span>
              </div>
            </div>,
          ]}
        </ToolkitProvider>
      </CardBody>
    </Card>
  );
};

export default LancamentoItensDataGrid;
