import { ACTION_EVENTS, alertManager, AlertType, compare, formatDate, parseLastDay, parseRefToDate } from 'summer';
import { FORA_MES_ATUAL, FORA_PERIODO_FISCAL, PERIODO_ANTERIOR } from '../models/enumerated/mcg/bloqueioLancamentoValidateStringEnum';
import { APENAS_PERIODO_FISCAL, APENAS_REF_ATUAL, TODOS } from '../models/enumerated/mcg/tipoBloqueioLancamentoEnum';

export const validatePeriodoConfig = (data, globalParameter) => {
  const config = globalParameter.lancamentoMCGConfig;
  const refInicial = formatDate(parseRefToDate(globalParameter.empresaMCG.refAtual, 1));
  const refInicialUltimoDia = formatDate(parseLastDay(globalParameter.empresaMCG.refAtual));
  const refFinal = formatDate(parseLastDay(globalParameter.empresaMCG.refFinal));

  let result = null;

  if (config.tipoBloqueioLancamento === APENAS_PERIODO_FISCAL.value && (compare(data, refInicial) < 0 || compare(data, refFinal) > 0)) {
    result = FORA_PERIODO_FISCAL.label;
  } else if (config.tipoBloqueioLancamento === APENAS_REF_ATUAL.value && (compare(data, refInicial) < 0 || compare(data, refInicialUltimoDia) > 0)) {
    result = FORA_MES_ATUAL.label;
  } else if (config.tipoBloqueioLancamento === TODOS.value && compare(data, refInicial) < 0) {
    result = PERIODO_ANTERIOR.label;
  }

  return result;
};

export const concatenarHistorico = (list: any[]) => {
  const itens = list.map(
    it => ((it.historico ? it.historico.descricao : it.descricao) || '') + (it.historicoVariavel ? ' ' + it.historicoVariavel : '')
  );
  let result = '';
  if (itens.length > 0) {
    result = itens.length > 1 ? itens.reduce((res, it) => res + ' ' + it) : itens[0];
  }
  return result;
};

export const promisedAlertWithCatch = (message, type = AlertType.WARNING_YES_NO) =>
  new Promise((resolve, reject) => {
    alertManager.emit(ACTION_EVENTS.SHOW, {
      message,
      onCancelClick: () => reject(),
      onNoClick: () => reject(),
      onOkClick: () => resolve(),
      type,
    });
  });

export const alertInformation = (message, type = AlertType.INFORMATION) => {
  alertManager.emit(ACTION_EVENTS.SHOW, {
    message,
    type,
  });
};
